@import url(https://fonts.googleapis.com/css?family=Montserrat:400,600,800i);
@import url(https://fonts.googleapis.com/css?family=Open+Sans:800);
body {
  margin: 0;
  padding: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  background: #fcfcfc !important;
  font-family: 'Open Sans', sans-serif;
}

h1, h2, h3, h4, h5, h6 {
  font-family: 'Montserrat', sans-serif !important;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}
.navbar__fix {
    position: fixed;
    bottom: 0;
    z-index: 999;
    border-radius: 0;
}

.navbar__fix.ui.menu {
    border-radius: 0;
    border-top: 1px solid rgba(34, 36, 38, 0.1490196078431);
    border-left: none;
    border-right: none;
    box-shadow: 0 -2px 8px 0 rgba(00, 00, 00, 0.1)
}

.view__container {
    padding-bottom: 100px;
}

.activity__button {
    z-index: 1000;
    position: fixed;
    bottom: 20px;
    border-radius: 163px;
    padding: 22px;
    right: 46%;
    background: lightcoral;
    color: white;
    font-size: 30px;
}

.slick-slide {
    margin: 20px 10px 0 -15px;
}

.slick-list {
    max-height: 250px;
    margin-bottom: 20px;
    padding: 0 !important;
}

.event__card__container {
    background-color: #FFF;
    border-radius: 4px;
    box-shadow: 0px 0px 25px 0px rgba(0, 0, 0, 0.1);
    margin: 20px auto;
    display: -ms-flexbox;
    height: 130px;
    display: flex;
    overflow: hidden;
    font-size: 1rem;
}

.event__card__container p {
    margin-bottom: 0;
}

.event__card__container .event__card__image {
    width: 50%;
    background-repeat: no-repeat;
    background-position: center;
    object-fit: cover;
    overflow: hidden;
}

.event__card__container .event__card__content__wrapper {
    display: flex;
    flex-direction: column;
    justify-content: space-between;
    /* padding: 20px 40px 20px 20px; */
    padding: 20px;
    width: 100%;
}

.event__card__content__wrapper .sub__info__wrapper {
    display: flex;
    justify-content: space-between;
}

.fake__background {
    background-color: rgba(0, 0, 0, 0.3);
    width: 100vw;
    height: 100vh;
    z-index: 1;
    position: absolute;
    top: 0;
}

.modal__view {
    background-color: #FCFCFC;
    width: 100vw;
    height: auto;
    max-height: 80vh;
    z-index: 2;
    position: absolute;
    bottom: 0;
    border-radius: 8px 8px 0 0;
    /* overflow-x: hidden;  */
}

.modal__view .container {
    padding-bottom: 7rem;
}

.icon__home:after {
    content: url(/static/media/home.6cacc21b.svg);
}

.icon__location:after {
    content: url(/static/media/location.05e8e37c.svg);
}

.icon__points:after {
    content: url(/static/media/points.32507599.svg);
}

.icon__user:after {
    content: url(/static/media/user.ea0f4772.svg);
}

.icon__plus:after {
    content: url(/static/media/plus.6b75f594.svg);
}
.icon__plus.white {
    color: white;
}

.icon__cross:after {
    content: url(/static/media/cross.84e833e5.svg);
}

.icon__check:after {
    content: url(/static/media/check.42723ec7.svg);
}

i.icon {
    margin: 0 !important
}


.App {
    overflow: hidden;
    background-color: #fcfcfc;
}

